*
{
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  box-sizing: border-box;
}

.todos-container
{
  width: clamp(10rem, 50vw, 20rem);


  position: absolute;
  inset: 1rem 0 0 0;
  margin: 0 auto;
  min-height: clamp(10rem, 40vh, 20rem);
  height: fit-content;

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;

  border: 5px dashed rgb(136, 136, 136);
}

.todos-container form
{
  position: relative;
  
  width: 80%;
  height: 40%;
  margin-bottom: 2%;

  display: flex;
  flex-direction: column;

}
.todos-container form input[type="text"]
{
  margin-top: .25rem;
  border: .2rem solid rgb(113, 126, 126);
  width: 70%;
  align-self: center;
}
.todos-container input[type="submit"]
{
  width: 50%;
  align-self: center;
  position: relative;
  top: 1rem;
}
.todos-container ul
{
  position: relative;
  top: 1%;
  display: flex;
  flex-direction: column;
  width: auto;
  overflow-x: hidden;
}
.todos-container ul li
{
  list-style-type: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  width: fit-content;
  line-height: 1.3rem;
}
.todos-container ul li input
{
  position: relative;
  left: .25rem;
  align-self: flex-end;
  
}
.todos-container button
{
  margin-top: 1rem;
}